import { mapGetters } from 'vuex'

export default {
  name: 'compareDeckModal',
  data() {
    return {
      changeModel: false,
      isModel: false,
    };
  },
  computed: {
    ...mapGetters([
      'compareDeck1',
      'compareDeck2'
    ]),
    deck1Code() {
      return this.compareDeck1?this.compareDeck1.deck_code:""
    },
    deck2Code() {
      return this.compareDeck2?this.compareDeck2.deck_code:""
    },
    disableCompare() {
      return this.compareDeck1 === null || this.compareDeck2 === null;
    },
    cancelBtnText() {
      // if (this.deck1Code === "" || this.deck2Code === "") {
      //   return '下一个'
      // } else {
      //   return '取消'
      // }
      return '确定'
    }
  },
  methods: {
    //  弹框取消
    tapCancel() {
      this.changeModel = false;
      this.$emit('cancel')
    },
    tapClear() {
      console.log('tabClear')
      this.$emit('clear')
    },
    //  确认
    confirmSend() {
      if (!this.disableCompare) {
        this.changeModel = false;
        this.$emit('confirm')
      }
    },
    showModal() {
      this.changeModel = true;
      this.isModel = true;
    },
    addCompareDeck1() {
      this.$emit('addCompareDeck1')
    },
    addCompareDeck2() {
      this.$emit('addCompareDeck2')
    }
  }
}
